// contains modifications for internal landing pages, ie the case studies page
.view-id-case_studies {
  .views-row {
      display: inline-block;
      vertical-align: top;
      text-align: left;
      padding: 0;
      margin: 12px 42px 0 0;
      width: 242px;
  }

  .views-field-title h4 {
      line-height: 1.25em;
      margin: .5em 0 .5em 0;
      font-size: 1.2em;
      font-weight: normal;
  }

  .views-field-view-node {
      font-size: 16px;
      margin: .5em auto;
  }
}

.view-id-workflow_guide, .view-id-actions {
    .group-left {
	width: 100%;
    }

    .group-right {
	float: right;
	clear: right;
    }
}

.page-case-studies h1#page-title,
.page-workflow-guide h1#page-title,
.page-actions h1#page-title {
    width: 1170px;
    max-width: 100%;
    font-size: 2.5em;
    margin: .5em auto;
}
